import React, {useEffect, useState} from "react";
import classes from './Godine.module.css'

const Godine = props => {
    const updatedGodine = props.godine
    useEffect(() => {

    }, [])
    const onClickGodina = (event) => {
        const value = event.target.textContent
        const godinaDiv = document.getElementById('godina-div')
        const godine = godinaDiv.getElementsByTagName('p')
        for(let i = 0; i < godine.length; i++){

            if(godine[i].textContent === value) {
                godine[i].classList.add(classes.Active)
            }
            else
                godine[i].classList.remove(classes.Active)
        }
    }
    return (
        <div id='godina-div' className={classes.Godine}>
            {
                updatedGodine.map((godina, i) => {
                    return (
                        <p
                            onClick={(event) => {
                                onClickGodina(event)
                                props.clicked(godina.godina)
                            }}
                            className={classes.GodineItem}
                            key={i}>{godina.godina}
                        </p>
                    )
                })
            }
        </div>
    )
}

export default Godine