import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

import {Provider} from 'react-redux'
import {createStore, applyMiddleware, compose, combineReducers} from "redux";
import rasporedReducer from './store/reducers/raspored_reducer'
import pageReducer from "./store/reducers/page_reducer";
import izborneGrupeReducer from './store/reducers/izbornegrupe_reducer'
import {BrowserRouter} from "react-router-dom";
import '@fortawesome/fontawesome-free/css/all.min.css';
import 'bootstrap-css-only/css/bootstrap.min.css';
import 'mdbreact/dist/css/mdb.css';
import thunk from 'redux-thunk'

const composeEnhancers = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const rootReducer = combineReducers({
    rasporedReducer: rasporedReducer,
    pageReducer: pageReducer,
    izborneGrupeReducer: izborneGrupeReducer
})
const store = createStore(
    rootReducer, composeEnhancers(
        applyMiddleware(thunk)
    )
)
const app = (
    <Provider store={store}>
        <BrowserRouter>
            <App/>
        </BrowserRouter>
    </Provider>
)
ReactDOM.render(app, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
