import * as actionTypes from '../actions/actionTypes_action'
import axios from 'axios'

export const fetchIzborneGrupeStart = () => {
    return {
        type: actionTypes.FETCH_IZBORNE_GRUPE_START
    }
}

export const fetchIzborneGrupeSuccess = (data) => {
    return {
        type: actionTypes.FETCH_IZBORNE_GRUPE_SUCCESS,
        data: data
    }
}

export const fetchIzborneGrupeFail = (error) => {
    return {
        type: actionTypes.FETCH_IZBORNE_GRUPE_FAIL,
        error: error
    }
}

export const fetchIzborneGrupe = () => {
   return dispatch => {
       dispatch(fetchIzborneGrupeStart())
       axios.get('http://127.0.0.1:8000/studserviceapp-api/izmena-grupe/')
           .then(response => {
               dispatch(fetchIzborneGrupeSuccess(response.data))
           })
           .catch(error => {
               dispatch(fetchIzborneGrupeFail(error))
           })
   }
}
