import React from 'react'
import classes from './SideNav.module.css'
import SideNavMenu from "./SideNavMenu/SideNavMenu";
import SideNavLogo from "./SideNavLogo/SideNavLogo";
import Backdrop from "../../UI/Backdrop/Backdrop";

const SideNav = props => {
    let attachedClasses = [classes.SideNav, classes.Closed].join(' ')
    if(props.showSideNav){
        attachedClasses = [classes.SideNav, classes.Open].join(' ')
    }
    let sideNavCollapsible = (
        <React.Fragment>
            <Backdrop show={props.showBackdrop} backdropClicked={props.onBackdropClicked}/>
            <div className={attachedClasses}>
                <ul className={classes.ListView}>
                    <SideNavLogo/>
                    <SideNavMenu onBackdropClicked={props.onBackdropClicked}/>
                </ul>
            </div>
        </React.Fragment>
    )
    return (
        <React.Fragment>
            {sideNavCollapsible}
        </React.Fragment>
    )
}

export default SideNav