import * as actonTypes from '../actions/actionTypes_action'

const initialState = {
    grupe_po_godinama: [],
    loading: false,
    error: ''
}

const reducer = (state = initialState, action) => {
    switch (action.type) {
        case actonTypes.FETCH_IZBORNE_GRUPE_START:
            return {
                ...state,
                loading: true
            }
        case actonTypes.FETCH_IZBORNE_GRUPE_SUCCESS:
            const grupe_po_godinama = {
                prvaGodina: {
                    godina: 'I godina',
                    grupe: []
                },
                drugaGodina: {
                    godina: 'II godina',
                    grupe: []
                },
                trecaGodina: {
                    godina: 'III godina',
                    grupe: []
                },
                cetvrtaGodina: {
                    godina: 'IV godina',
                    grupe: []
                }
            }
            const grupe = action.data
            grupe.forEach(grupa => {
                const prefix = parseInt(grupa.oznaka_grupe[0])
                if (prefix === 1) {
                    grupe_po_godinama['prvaGodina'].grupe.push(grupa)
                } else if (prefix === 2) {
                    grupe_po_godinama['drugaGodina'].grupe.push(grupa)
                } else if (prefix === 3) {
                    grupe_po_godinama['trecaGodina'].grupe.push(grupa)
                } else if (prefix === 4) {
                    grupe_po_godinama['cetvrtaGodina'].grupe.push(grupa)
                }
            })
            const grupe_po_godinama_array = []
            let key = null
            for (key in grupe_po_godinama) {
                grupe_po_godinama_array.push(
                    grupe_po_godinama[key]
                )
            }
            return {
                ...state,
                loading: false,
                grupe_po_godinama: grupe_po_godinama_array
            }
        case actonTypes.FETCH_IZBORNE_GRUPE_FAIL:
            return {
                ...state,
                loading: false,
                error: action.error
            }
        default:
            return state
    }
}

export default reducer