import React from "react";
import classes from './SideNavMenu.module.css'
import classesSideNav from '../SideNav.module.css'
import classesMenuItems from '../SideNavMenuItems/SideNavMenuItems.module.css'
import SideNavMenuItems from "../SideNavMenuItems/SideNavMenuItems";

const SideNavMenu = (props) => {
    const handleDropdown = (event) => {
        let target = event.target
        if (event.target.tagName !== 'A') {
            target = event.target.parentElement
        }
        if (target.classList.contains(classesMenuItems.isDisabled)) {
            event.preventDefault()
        }
        const sideNav = document.getElementsByClassName(classesSideNav.SideNav)[0]
        const elements = sideNav.getElementsByClassName(classesMenuItems.active)
        const icon = target.getElementsByTagName('i')[1]

        for (let i = 0; i < elements.length; i++) {
            if (elements[i] !== target) {
                const parent = elements[i].parentElement
                const menu = parent.getElementsByClassName(classesMenuItems.CollapsibleMenu)[0]
                menu.style.display = 'none'
                const elementIcon = elements[i].getElementsByTagName('i')[1]
                elementIcon.classList.remove('fa-angle-up')
                elementIcon.classList.add('fa-angle-down')
                elements[i].classList.remove(classesMenuItems.active)
            }
        }

        if (target.classList.contains(classesMenuItems.active)) {
            target.classList.remove(classesMenuItems.active)
            const parent = target.parentElement
            const menu = parent.getElementsByClassName(classesMenuItems.CollapsibleMenu)[0]
            menu.style.display = 'none'
            icon.classList.remove('fa-angle-up')
            icon.classList.add('fa-angle-down')

        } else {
            target.classList.add(classesMenuItems.active)
            const parent = target.parentElement
            const menu = parent.getElementsByClassName(classesMenuItems.CollapsibleMenu)[0]
            menu.style.display = 'block'
            icon.classList.remove('fa-angle-down')
            icon.classList.add('fa-angle-up')
        }
    }

    return (
        <li>
            <ul className={classes.MenuView}>
                <SideNavMenuItems
                    faClass='fas fa-school'
                    label='Raspored'
                    onBackdropClicked={props.onBackdropClicked}
                    path='/studserviceapp' />

                <SideNavMenuItems
                    faClass='fas fa-graduation-cap'
                    label='Spisak studenata'
                    onBackdropClicked={props.onBackdropClicked}
                    path='/'/>

                <SideNavMenuItems
                    isDropdown={true}
                    faClass='fas fa-users'
                    label='Grupe'
                    onBackdropClicked={props.onBackdropClicked}
                    listItems={[
                        {name: 'Unos izbornih grupa', path:'/studserviceapp/unos-grupe'},
                        {name: 'Pregled izbornih grupa', path:'/studserviceapp/izborne-grupe'},
                        {name: 'Pregled izabranih grupa', path:'/'}
                    ]}
                    clicked={handleDropdown}/>

                <SideNavMenuItems
                    isDropdown={true}
                    faClass='fas fa-calendar-alt'
                    label='Ucitavanje rasporeda'
                    onBackdropClicked={props.onBackdropClicked}
                    listItems={[
                        {name: 'Kolokvijumi i ispiti', path:'/'},
                        {name: 'Predavanja', path:'/'}
                    ]}
                    clicked={handleDropdown}/>

                <SideNavMenuItems
                    faClass='fas fa-envelope'
                    label='E-mail'
                    onBackdropClicked={props.onBackdropClicked}
                    path='/'/>

                <SideNavMenuItems
                    faClass='fas fa-users-cog'
                    label='Administracija'
                    onBackdropClicked={props.onBackdropClicked}
                    path='/'/>

                <SideNavMenuItems
                    isDropdown={true}
                    faClass='fas fa-bell'
                    label='Obavestenja'
                    onBackdropClicked={props.onBackdropClicked}
                    listItems={[
                        {name: 'Unos obavestenja', path:'/'},
                        {name: 'Pregled obavestenja', path:'/'}
                    ]}
                    clicked={handleDropdown}/>
            </ul>
        </li>
    )
}

export default SideNavMenu