import React, {useEffect} from "react";
import classes from './Pocetna.module.css'
import Raspored from "../../components/Raspored/Raspored";
import {connect} from 'react-redux'
import * as actions from '../../store/actions/index'

const Pocetna = (props) => {
    useEffect(() => {
        props.onPageLoad('Raspored')
    }, [props])
    useEffect(() => {
        document.title = props.pageTitle
    }, [props.pageTitle])
    return (
        <div className={classes.Pocetna}>
            <Raspored/>
        </div>
    )
}

const mapStateToProps = state => {
    return {
        pageTitle: state.pageReducer.pageTitle
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onPageLoad: (title) => dispatch(actions.onPageLoad(title))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Pocetna)