import * as actionTypes from './actionTypes_action'
import axios from 'axios'

export const fetchRasporedStart = () => {
    return {
        type: actionTypes.FETCH_RASPORED_START
    }
}

export const fetchRasporedSuccess = (data) => {
    return {
        type: actionTypes.FETCH_RASPORED_SUCCESS,
        termini: data
    }
}

export const fetchRasporedFail = (error) => {
    return {
        type: actionTypes.FETCH_RASPORED_FAIL,
        error: error
    }
}

export const fetchRaspored = () => {
    return dispatch => {
        dispatch(fetchRasporedStart())
        axios.get('http://127.0.0.1:8000/studserviceapp-api/raspored')
            .then(response => {
                dispatch(fetchRasporedSuccess(response.data))
            })
            .catch(error => {
                dispatch(fetchRasporedFail(error))
            })
    }
}