import * as actionTypes from '../actions/actionTypes_action'

const initialState = {
    raspored: [],
    loading: false,
    error: null
}
const rasporedReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.FETCH_RASPORED_START:
            return {
                ...state,
                loading: true,
                error: null
            }
        case actionTypes.FETCH_RASPORED_SUCCESS:
            const fetchedData = action.termini
            const updatedData = fetchedData.map(item => {
                const lastGroup = item.grupe[item.grupe.length - 1].oznaka_grupe
                const {id, ...noId} = item
                return {
                    ...noId,
                    grupe: item.grupe.reduce((result, grupa) => {
                        return grupa.oznaka_grupe !== lastGroup ? result + grupa.oznaka_grupe + ', ' : result + grupa.oznaka_grupe
                    }, '')
                }
            })
            return {
                ...state,
                raspored: updatedData,
                loading: false,
                error: null
            }
        case actionTypes.FETCH_RASPORED_FAIL:
            return {
                ...state,
                loading: false,
                error: action.error
            }
        default:
            return state
    }
}

export default rasporedReducer