import React, {useState} from "react";
import Fieldset from "../Fieldset/Fieldset";
import Select from "../Select/Select";
import classes from './SharableLists.module.css'
const SharableList = props => {

    const [providerListSelected, setProviderListSelected] = useState([])
    const [consumerListSelected, setConsumerListSelected] = useState([])

    const listProviderSelected = []
    const listConsumerSelected = []

    const predmeti = props.predmeti

    const listObjectProvider = predmeti.elements[0]
    const listObjectConsumer = predmeti.elements[1]

    const [providerList, setProviderList] = useState(listObjectProvider.options)
    const [consumerList, setConsumerList] = useState(listObjectConsumer.options)

    const addToProvider = (valueArr, listOfElements = listProviderSelected) => {
        listOfElements.splice(0, listOfElements.length)
        for(let i = 0; i < valueArr.length; i++){
            listOfElements.push({value: valueArr[i], displayValue: valueArr[i]})
        }
        setProviderListSelected(listOfElements)
        console.log('Provider', listOfElements)
    }
    const addToConsumer = (valueArr, listOfElements = listConsumerSelected) => {
        listOfElements.splice(0, listOfElements.length)
        for(let i = 0; i < valueArr.length; i++){
            listOfElements.push({value: valueArr[i], displayValue: valueArr[i]})
        }
        setConsumerListSelected(listOfElements)
        console.log('Consumer', listOfElements)
    }
    const listElementProvider = (
        <div className={classes.SharableListItem}>
            <Select
                id={listObjectProvider.name}
                changed={() => {}}
                multiselectChanged={addToProvider}
                size={listObjectProvider.size}
                options={providerList}
                label={listObjectProvider.label}
                displayedList={true}
                displayedFilter={true}
                displayedPlaceholder={false}
                withFilter={true} />
        </div>
    )

    const listElementConsumer = (
        <div className={classes.SharableListItem}>
            <Select
                id={listObjectConsumer.name}
                changed={() => {}}
                multiselectChanged={addToConsumer}
                size={listObjectConsumer.size}
                options={consumerList}
                label={listObjectConsumer.label}
                displayedList={true}
                displayedFilter={true}
                displayedPlaceholder={false}
                withFilter={true} />
        </div>
    )
    const buttonGroup1_classes = []
    const buttonGroup2_classes = []
    const buttonGroup3_classes = []
    if(providerListSelected.length === 0){
        buttonGroup1_classes.push(classes.Disabled)
    }
    if(consumerListSelected.length === 0){
        buttonGroup2_classes.push(classes.Disabled)
    }
    if(consumerList.length === 0){
        buttonGroup3_classes.push(classes.Disabled)
    }
    const onControlClicked = (event, type) => {
        event.preventDefault()
        switch (type) {
            case 'ADD':
                 let newArrayProvider = providerList.filter(item => {
                    for(let i = 0; i < providerListSelected.length; i++){
                        if(providerListSelected[i].displayValue === item.displayValue){
                            return false
                        }
                    }
                    return true
                })
                setProviderList(newArrayProvider)
                let newArrayConsumer = consumerList.concat(providerListSelected)
                setConsumerList(newArrayConsumer)
                setProviderListSelected([])
                props.onMultiSelectChanged(newArrayConsumer)
                break
            case 'REMOVE':
                let newArrayConsumerRemove = consumerList.filter(item => {
                    for(let i = 0; i < consumerListSelected.length; i++){
                        if(consumerListSelected[i].displayValue === item.displayValue)
                            return false
                    }
                    return true
                })
                setConsumerList(newArrayConsumerRemove)
                let newArrayProviderRemove = providerList.concat(consumerListSelected)
                setProviderList(newArrayProviderRemove)
                setConsumerListSelected([])
                props.onMultiSelectChanged(newArrayConsumerRemove)
                break
            case 'REMOVE_ALL':
                let newArrayProviderRemoveAll = providerList.concat(consumerList)
                setProviderList(newArrayProviderRemoveAll)
                setConsumerList([])
                props.onMultiSelectChanged([])
                break
            default:
                break
        }
    }
    const controlsRow = (
        <div className={classes.SharableControlsDesktop}>
            <button onClick={(event) => onControlClicked(event, 'ADD')} className={buttonGroup1_classes}><i className='fas fa-angle-right'></i></button>
            <button onClick={(event) => onControlClicked(event, 'REMOVE')} className={buttonGroup2_classes}><i className='fas fa-angle-left'></i></button>
            <button onClick={(event) => onControlClicked(event, 'REMOVE_ALL')} className={buttonGroup3_classes}><i className='fas fa-angle-double-left'></i></button>
        </div>
    )
    const controlsColumn = (
        <div className={classes.SharableControlsMobile}>
            <button onClick={(event) => onControlClicked(event, 'ADD')} className={buttonGroup1_classes}><i className='fas fa-angle-down'></i></button>
            <button onClick={(event) => onControlClicked(event, 'REMOVE')} className={buttonGroup2_classes}><i className='fas fa-angle-up'></i></button>
            <button onClick={(event) => onControlClicked(event, 'REMOVE_ALL')} className={buttonGroup3_classes}><i className='fas fa-angle-double-up'></i></button>
        </div>
    )
    let toRender = (
        <div className={classes.SharableList}>
            {listElementProvider}
            {controlsRow}
            {listElementConsumer}
        </div>
    )
    if(props.fieldset){
        toRender = (
            <Fieldset legend={props.fieldsetName}>
                <div className={classes.SharableList}>
                    {listElementProvider}
                    {controlsRow}
                    {controlsColumn}
                    {listElementConsumer}
                </div>
            </Fieldset>
        )
    }
    return (
        toRender
    )
}

export default SharableList