import React from "react";
import classes from "./SideNavMenuItems.module.css";
import {Link} from "react-router-dom";
import classesMenu from '../SideNavMenu/SideNavMenu.module.css'
const SideNavMenuItems = (props) => {
    const combineClasses = (other) => {
        return other.join(' ')
    }
    let item
    if(props.isDropdown){
        item = (
            <li className={classes.Dropdown}>
                <a href=' ' className={classes.isDisabled} onClick={props.clicked}>
                    <i className={props.faClass}></i>
                    {props.label}
                    <i className={combineClasses(['fas fa-angle-down', classesMenu.rotateIcon])}></i>
                </a>
                <div className={classes.CollapsibleMenu}>
                    <ul>
                        {props.listItems.map((listItem, i) => {
                            return <li onClick={props.onBackdropClicked} key={i}><Link to={listItem.path}>{listItem.name}</Link></li>
                        })}
                    </ul>
                </div>
            </li>
        )
    }else{
       item =  (
            <li onClick={props.onBackdropClicked}>
                <Link to={props.path}>
                    <i className={props.faClass}></i>
                    {props.label}
                </Link>
            </li>
        )
    }
    return (
        <React.Fragment>
            {item}
        </React.Fragment>
    )
}

export default SideNavMenuItems