import React, {useEffect, useState} from "react";
import Modal from "../../components/UI/Modal/Modal";
import * as actions from "../../store/actions";
import {connect} from 'react-redux'
import Input from "../../components/UI/Input/Input";
import SharableList from "../../components/UI/SharableLists/SharableLists";
import RadioGroup from "../../components/UI/RadioGroup/RadioGroup";
import classes from './UnosIzborneGrupe.module.css'
import Select from "../../components/UI/Select/Select";
import Spinner from "../../components/UI/Spinner/Spinner";
import Button from "../../components/UI/Button/Button";
import axios from 'axios'

const UnosIzborneGrupe = props => {
    const [loading, setLoading] = useState(true)
    const [submitting, setSubmitting] = useState(false)
    const [formData, setFormData] = useState({
        semestar: {
            fieldset: false,
            label: 'Semestar',
            placeholder: 'Odaberite semestar...',
            elementType: 'select',
            type: 'select',
            options: [
                {value: 'parni', displayValue: 'Parni semestar'},
                {value: 'neparni', displayValue: 'Neparni semestar'}
            ],
            shouldValidate: false,
            value: 'parni',
            size: 2
        },
        sk_pocetak: {
            fieldset: false,
            label: 'Skolska godina pocetak',
            elementType: 'input',
            type: 'text',
            placeholder: 'Skolska godina pocetak',
            shouldValidate: true,
            touched: false,
            validation: [
                {required: true, isYear: true}
            ],
            value: '',
            valid: false,
            errorMessage: ''
        },
        sk_kraj: {
            fieldset: false,
            label: 'Skolska godina kraj',
            elementType: 'input',
            type: 'text',
            placeholder: 'Skolska godina kraj',
            shouldValidate: true,
            touched: false,
            validation: [
                {required: true, isYear: true}
            ],
            value: '',
            valid: false,
            errorMessage: ''
        },
        smer: {
            fieldset: false,
            label: 'Smer',
            placeholder: 'Odaberite smer...',
            elementType: 'select',
            type: 'select',
            options: [
                {value: 'RN', displayValue: 'Racunarske nauke - RN'},
                {value: 'RM', displayValue: 'Racunarske mreze - RM'}
            ],
            shouldValidate: false,
            value: 'RN',
            size: 2
        },
        grupe: {
            fieldset: false,
            label: 'Grupe',
            elementType: 'input',
            type: 'text',
            placeholder: 'Oznaka grupe',
            tooltip: 'Moguce je uneti vise od jedne grupe. Primer: 301,302,...',
            shouldValidate: true,
            touched: false,
            validation: [
                {required: true, areGroups: true}
            ],
            valid: false,
            value: '',
            errorMessage: ''
        },
        kapacitet: {
            fieldset: false,
            label: 'Kapacitet',
            elementType: 'input',
            type: 'text',
            placeholder: 'Kapacitet grupe',
            shouldValidate: true,
            touched: false,
            validation: [
                {required: true, isNumber: true}
            ],
            value: '',
            valid: false,
            errorMessage: ''
        },
        predmeti: {
            fieldset: true,
            fieldsetName: 'Predmeti grupe',
            label: 'Predmeti grupe',
            elementType: 'multiselect',
            type: 'multiselect',
            elements: [
                {
                    name: 'odabir',
                    provider: true,
                    consumer: false,
                    hasFilter: true,
                    options: [],
                    size: 5,
                    label: 'Odabir predmeta'
                },
                {
                    name: 'odabrani',
                    provider: false,
                    consumer: true,
                    hasFilter: false,
                    options: [],
                    size: 5,
                    label: 'Odabrani predmeti'
                }
            ],
            shouldValidate: false,
            value: []
        },
        aktivnost: {
            fieldset: true,
            label: 'Aktivnost grupe',
            name: 'aktivnost',
            elementType: 'radio',
            type: 'radio',
            elements: [
                {value: 'aktivna', displayValue: 'Aktivna', checked: true},
                {value: 'neaktivna', displayValue: 'Neaktivna', checked: false}
            ],
            shouldValidate: false,
            value: 'aktivna'
        }
    })
    useEffect(() => {
        props.onPageLoad('Unos grupe')
        axios.get('http://127.0.0.1:8000/studserviceapp-api/predmeti')
            .then(response => {
                const data = response.data
                for (let i = 0; i < data.length; i++) {
                    data[i] = {
                        value: data[i].naziv,
                        displayValue: data[i].naziv
                    }
                }
                const oldFormData = {
                    ...formData
                }
                oldFormData.predmeti.elements[0].options = data
                setFormData(oldFormData)
                setLoading(false)
            })
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [props])

    useEffect(() => {
        document.title = props.pageTitle
    }, [props.pageTitle])

    const formDataToArray = () => {
        const array = []
        let key = null
        for (key in formData) {
            array.push({
                id: key,
                data: formData[key]
            })
        }
        return array
    }
    const formDataArray = formDataToArray()
    const onChangeHandler = (event, id) => {
        const data = {
            ...formData
        }

        const updatedElement = {
            ...data[id]
        }
        const secondElement = {
            ...data['sk_kraj']
        }
        updatedElement.value = event.target.value
        if(id === 'sk_pocetak'){
            secondElement.value = parseInt(event.target.value) + 1
            data['sk_kraj'] = secondElement
        }
        data[id] = updatedElement
        setFormData(data)
    }
    const onSelectChangeHandler = (value, id) => {
        const data = {
            ...formData
        }
        const updatedElement = {
            ...data[id]
        }
        updatedElement.value = value
        data[id] = updatedElement
        setFormData(data)
    }
    const onMultiSelectChanged = (value, id = 'predmeti') => {
        const data = {
            ...formData
        }
        const updatedElement = {
            ...data[id]
        }
        updatedElement.value = value
        data[id] = updatedElement
        setFormData(data)
    }
    const onRadioHandler = (event, id) => {
        const data = {
            ...formData
        }
        const updatedElement = {
            ...data[id]
        }
        const value = event.target.value
        let key = null
        for (key in updatedElement.elements) {
            if (updatedElement.elements[key].value === value) {
                updatedElement.elements[key].checked = true
            } else {
                updatedElement.elements[key].checked = false
            }
        }
        updatedElement.value = value
        data[id] = updatedElement
        setFormData(data)
    }
    const modalClosed = () => {
        setSubmitting(false)
    }
    const onSubmitting = (event) => {
        event.preventDefault()
        setSubmitting(true)
    }
    const onSubmit = (event) => {
        event.preventDefault()
        const data = {
            semestar: formData.semestar.value,
            skolska_godina_pocetak: formData.sk_pocetak.value,
            skolska_godina_kraj: formData.sk_kraj.value,
            smer: formData.smer.value,
            grupe: formData.grupe.value,
            kapacitet: formData.kapacitet.value,
            predmeti: formData.predmeti.value,
            aktivnost: formData.aktivnost.value
        }
        console.log(data)
        axios.post('http://127.0.0.1:8000/studserviceapp/unos-grupe/',data)
            .then(response => {
                console.log(response)
            })
            .catch(error => {
                console.log(error)
            })
        modalClosed()
        props.history.push('/studserviceapp')
    }
    let form = <Spinner/>
    if (!loading) {
        form = (
            <form onSubmit={onSubmit} id="unos-grupe-form">
                {
                    // eslint-disable-next-line array-callback-return
                    formDataArray.map((item, i) => {
                        const itemType = item.data.elementType
                        if (itemType === 'input') {
                            return <Input
                                key={item.id}
                                elementType={item.data.elementType}
                                type={item.data.type}
                                placeholder={item.data.placeholder}
                                shouldValidate={item.data.shouldValidate}
                                value={item.data.value}
                                changed={(event) => onChangeHandler(event, item.id)}
                                invalid={!item.data.valid}
                                touched={item.data.touched}
                                errorMessage={item.data.errorMessage}
                                label={item.data.label}
                            />
                        } else if (itemType === 'select') {
                            return <Select
                                key={item.id}
                                id={item.id}
                                elementType={item.data.elementType}
                                type={item.data.type}
                                value={item.data.value}
                                changed={onSelectChangeHandler}
                                multiselectChanged={() => {
                                }}
                                size={item.data.size}
                                options={item.data.options}
                                placeholder={item.data.placeholder}
                                label={item.data.label}
                                displayedList={false}
                                displayedFilter={false}
                                displayedPlaceholder={true}
                                withFilter={false}
                            />
                        } else if (itemType === 'radio') {
                            return <RadioGroup
                                key={item.id}
                                options={item.data.elements}
                                name={item.data.name}
                                fieldset={item.data.fieldset}
                                fieldsetName={item.data.label}
                                changed={(event) => onRadioHandler(event, item.id)}
                            />
                        } else if (itemType === 'multiselect') {
                            return <SharableList
                                key={item.id}
                                predmeti={item.data}
                                fieldset={item.data.fieldset}
                                fieldsetName={item.data.fieldsetName}
                                onMultiSelectChanged={onMultiSelectChanged}/>
                        }
                    })
                }
                <Button buttonType='Success' clicked={onSubmitting}>UNESI</Button>
            </form>
        )
    }
    return (
        <div className={classes.UnosIzborneGrupe}>
            <Modal modalClosed={modalClosed} show={submitting}>
                <div className={classes.ModalView}>
                    <div className={classes.ModalContent}>
                        <h3>Grupa</h3>
                        <p><span>Semestar:</span> {formData.semestar.value}</p>
                        <p><span>Sk_Pocetak:</span> {formData.sk_pocetak.value}</p>
                        <p><span>Sk_kraj:</span> {formData.sk_kraj.value}</p>
                        <p><span>Smer:</span> {formData.smer.value}</p>
                        <p><span>Grupe:</span> {formData.grupe.value}</p>
                        <p><span>Kapacitet:</span> {formData.kapacitet.value}</p>
                        <p><span>Predmeti:</span></p>
                        <ul>
                            {formData.predmeti.value.map(item => {
                                    return <li key={item.displayValue}>{item.displayValue}</li>
                                }
                            )}
                        </ul>
                        <p><span>Aktivnost:</span> {formData.aktivnost.value}</p>
                    </div>
                    <div className={classes.ModalActions}>
                        <Button clicked={onSubmit} buttonType='Success'>POTVRDI</Button>
                        <Button clicked={modalClosed} buttonType='Danger'>OTKAZI</Button>
                    </div>
                </div>

            </Modal>
            {form}
        </div>
    )
}
const mapStateToProps = state => {
    return {
        pageTitle: state.pageReducer.pageTitle
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onPageLoad: (title) => dispatch(actions.onPageLoad(title))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(UnosIzborneGrupe)