import React from "react";
import classes from './PregledGrupa.module.css'
import Godine from "./Godine/Godine";
import Grupe from "./Grupe/Grupe";

const PregledGrupa = props => {
    return (
        <div className={classes.PregledGrupa}>
            <Godine clicked={props.godinaOnClick} godine={props.godine}/>
            <i className={['fas', classes.Indicator].join(' ')} />
            <Grupe onItemDeleteClicked={props.onItemDeleteClicked} clicked={props.grupaOnClick} grupe={props.grupe}/>
        </div>
    )
}
export default PregledGrupa