import React, {useState, useEffect} from "react";
import classes from './Select.module.css'

const Select = props => {
    const [placeholder, setPlaceholder] = useState('')
    const [options, setOptions] = useState(props.options)
    const [filteredList, setFilteredList] = useState(props.options)

    useEffect(() => {
        setOptions(props.options)
        setFilteredList(props.options)
    }, [props.options])
    const listHeight = props.size * 36
    const sizeStyle = {
        height: listHeight+'px'
    }
    useEffect(() => {
        setPlaceholder(props.placeholder)
    }, [props.placeholder])

    const onPlaceHolderClicked = (event) => {
        let parentElement = event.target.parentElement
        if(parentElement.parentElement !== document.getElementById('unos-grupe-form')){
            parentElement = parentElement.parentElement
        }
        if (props.withFilter) {
            const filter = parentElement.getElementsByClassName(classes.SelectFilter)[0]
            if(filter) {
                if (filter.classList.contains(classes.DisplayNone)) {
                    filter.classList.remove(classes.DisplayNone)
                } else {
                    filter.classList.add(classes.DisplayNone)
                }
            }
        }
        const list = parentElement.getElementsByClassName(classes.SelectList)[0]
        if(!props.displayedList && list) {
            if (list.classList.contains(classes.DisplayNone)) {
                list.classList.remove(classes.DisplayNone)
            } else {
                list.classList.add(classes.DisplayNone)
            }
        }
    }
    const onItemClicked = (event) => {
        const parentElement = event.target.parentElement.parentElement.parentElement
        if (!props.withFilter) {
            const filter = parentElement.getElementsByClassName(classes.SelectFilter)[0]
            filter.classList.add(classes.DisplayNone)
        }
        const list = parentElement.getElementsByClassName(classes.SelectList)[0]
        if(!props.displayedList) {
            list.classList.add(classes.DisplayNone)
            const elements = list.getElementsByTagName('li')
            for(let i = 0; i < elements.length; i++){
                elements[i].classList.remove(classes.active)
            }
        }

        setPlaceholder(event.target.title)
        const element = event.target
        if(element.classList.contains(classes.active)){
            element.classList.remove(classes.active)
        }else{
            element.classList.add(classes.active)
        }
        props.changed(event.target.id, props.id)
        const elements = list.getElementsByTagName('li')
        const elArray = []
        for(let i = 0; i < elements.length; i++){
            if(elements[i].classList.contains(classes.active))
                elArray.push(elements[i].id)
        }
        props.multiselectChanged(elArray)
    }
    const onFilterChanged = (event) => {
        const value = event.target.value.toUpperCase()
        const filtered = options.filter(item => {
            return item.displayValue.toUpperCase().indexOf(value) > -1
        })
        setFilteredList(filtered)

    }
    let placeholderClasses = [classes.SelectPlaceholder]
    let filterClasses = [classes.SelectFilter]
    let listClasses = [classes.SelectList]
    if (!props.displayedPlaceholder) {
        placeholderClasses.push(classes.DisplayNone)
    }
    if (!props.displayedFilter) {
        filterClasses.push(classes.DisplayNone)
    }
    if (!props.displayedList) {
        listClasses.push(classes.DisplayNone)
    }
    return (
        <div className={classes.Select}>
            <label className={classes.Label}>{props.label}</label>
            <div onClick={onPlaceHolderClicked} className={placeholderClasses.join(' ')}>
                <div>{placeholder}</div>
                <i className='fas fa-angle-down'></i>
            </div>
            <div>
                <div className={filterClasses.join(' ')}>
                    <i className='fas fa-search'/>
                    <input onChange={onFilterChanged} className={classes.FilterInput} placeholder='Filter...'/>
                </div>
                <ul style={sizeStyle} className={listClasses.join(' ')}>
                    {
                        filteredList.map(option => {
                            return <li onClick={onItemClicked} title={option.displayValue} id={option.value} key={option.value}>{option.displayValue}</li>
                        })
                    }
                </ul>
            </div>
        </div>
    )
}

export default Select