import * as actionTypes from '../actions/actionTypes_action'

const initialState = {
    pageTitle: 'Studentski servis'
}

const pageReducer = (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.ON_PAGE_LOAD:
            return {
                ...state,
                pageTitle: action.title
            }
        default:
            return state
    }
}
export default pageReducer