import React, {useEffect, useState, useRef} from "react";
import classes from './Grupe.module.css'

const Grupe = props => {
    const [grupe, setGrupe] = useState(props.grupe)
    const [filtered, setFiltered] = useState(props.grupe)
    const filterElement = useRef(null)

    useEffect(() => {
        setFiltered(props.grupe)
        setGrupe(props.grupe)
        if(filterElement.current !== null)
            filterElement.current.value = ''
    }, [props])
    const onFilterChange = (event) => {
        const value = event.target.value.toUpperCase()
        const filter = grupe.filter(grupa => {
            return grupa.oznaka_grupe.toUpperCase().indexOf(value) > -1
        })
        setFiltered(filter)
    }
    let body = (
        <div className={classes.Grupe}>
            <div className={classes.NoGroups}>
                <p>Molimo Vas da odaberete godinu.</p>
            </div>
        </div>
    )
    if (props.grupe.length > 0) {
        body = (
            <div className={classes.Grupe}>
                <div className={classes.SelectFilter}>
                    <i className='fas fa-search'/>
                    <input ref={filterElement} onChange={onFilterChange} className={classes.FilterInput} placeholder='Filter...'/>
                </div>
                {
                    filtered.map((grupa, i) => {
                        return (
                            <div  key={i} className={classes.GrupeItems}>
                                <p
                                    className={classes.GrupeItem}
                                    onClick={() => props.clicked(grupa)}>
                                    <span>Grupa: {grupa.oznaka_grupe} {grupa.smer}</span>
                                    <span>Broj studenata: {grupa.broj_studenata}</span>
                                </p>
                                <span onClick={() => props.onItemDeleteClicked(grupa)} className={classes.DeleteItem}><i className={'fas fa-times'} /></span>
                            </div>
                        )
                    })
                }
            </div>
        )
    }
    return (
        body
    )
}

export default Grupe