import React from "react";
import Modal from "../../components/UI/Modal/Modal";

const NoMatch = props => {
    const onModalClosed = () => {
        props.history.replace('/studserviceapp')
    }
    return (
        <div>
            <Modal important={true} modalClosed={onModalClosed} show={true}>
                <p>Page not found!</p>
            </Modal>
        </div>
    )
}

export default NoMatch