import React from "react";
import Fieldset from "../Fieldset/Fieldset";
import classes from './RadioGroup.module.css'

const RadioGroup = props => {
    let group = (
        <div className={classes.RadioGroup}>
            {
                props.options.map((option, i) => {
                    return (
                        <div key={i} className={classes.RadioItem}>
                            <input onChange={props.changed} name={props.name} id={option.value} type='radio' value={option.value} checked={option.checked}/>
                            <label className={classes.Label} htmlFor={option.value}>{option.displayValue}</label>
                        </div>
                    )
                })
            }
        </div>
    )
    let element
    if (props.fieldset) {
        element = (
            <Fieldset legend={props.fieldsetName}>
                {group}
            </Fieldset>
        )
    } else {
        element = group
    }
    return (
        element
    )
}

export default RadioGroup