import React from 'react';
import Pocetna from "./container/Pocetna/Pocetna";
import {Route, Switch} from "react-router";
import NavbarMenu from "./components/Navbar/NavbarMenu/NavbarMenu";
import MainWrapper from "./components/UI/MainWrapper/MainWrapper";
import UnosIzborneGrupe from "./container/UnosIzborneGrupe/UnosIzborneGrupe";
import NoMatch from "./container/NoMatch/NoMatch";
import PregledIzbornihGrupa from "./container/IzmenaIzbornihGrupa/IzmenaIzbornihGrupa";

const App = () => {
    return (
        <React.Fragment>
            <NavbarMenu/>
            <MainWrapper>
                <Switch>
                    <Route path="/studserviceapp" exact component={Pocetna}/>
                    <Route path="/studserviceapp/unos-grupe" component={UnosIzborneGrupe}/>
                    <Route path="/studserviceapp/izborne-grupe" component={PregledIzbornihGrupa} />
                    <Route component={NoMatch} />
                </Switch>
            </MainWrapper>
        </React.Fragment>

    )
}

export default App

