import React, {useEffect} from "react";
import classes from './AccountItem.module.css'
import avatar from '../../../../assets/avatar.jpg'

const AccountItem = props => {
    useEffect(() => {
        document.addEventListener('click', (event) => {
            const item = document.getElementsByClassName(classes.AccountItem)[0]
            const modal = document.getElementsByClassName(classes.DropdownList)[0]
            const name = item.getElementsByTagName('p')[0]
            const img = item.getElementsByTagName('img')[0]
            if(!modal.contains(event.target) && event.target !== item && event.target !== modal && event.target !== img && event.target !== name){
                modal.style.display = 'none'
            }
        })
    }, [])
    const onAccountItemClicked = (event) => {
        const dropdown = document.getElementsByClassName(classes.DropdownList)[0]
        if (dropdown.style.display === 'none' || dropdown.style.display === '') {
            dropdown.style.display = 'block'
        }else{
            dropdown.style.display = 'none'
        }
    }
    return (
        <li>
            <div onClick={onAccountItemClicked} className={classes.AccountItem}>
                <p>Vladimir Dabic</p>
                <img src={avatar} alt='account logo'/>
            </div>
            <ul className={classes.DropdownList}>
                <li className={classes.AccountInfo}>
                    <img src={avatar} alt='account logo big'/>
                    <p>
                        <span className={classes.Name}>Vladimir Dabic</span><br/>
                        <span className={classes.Index}>RN 87/2017</span><br/>
                        <span className={classes.Mail}>vdabic17@raf.rs</span>
                    </p>
                </li>
                <li className={classes.Odjava}>Odjava</li>
            </ul>
        </li>
    )
}

export default AccountItem