import React, {useState, useEffect} from "react";
import {connect} from 'react-redux'
import * as actions from "../../store/actions";
import Spinner from "../../components/UI/Spinner/Spinner";
import classes from './IzmenaIzbornihGrupa.module.css'
import PregledGrupa from "../../components/PregledGrupa/PregledGrupa";
import Modal from "../../components/UI/Modal/Modal";
import IzmenaGrupe from "../../components/IzmenaGrupe/IzmenaGrupe";
import Button from "../../components/UI/Button/Button";
import axios from 'axios'

const IzmenaIzbornihGrupa = props => {

    // znaci svakako saljem post za brisanje ali setujem trenutnu listu bez tog elementa umesto da getujem
    const [grupePoGodinama, setGrupePoGodinama] = useState([])
    const [trenutneGrupe, setTrenutneGrupe] = useState([])
    const [grupaZaIzmenu, setGrupaZaIzmenu] = useState(null)
    const [menjanje, setMenjanje] = useState(false)
    const [brisanje, setBrisanje] = useState(false)
    const [grupaZaBrisanje, setGrupaZaBrisanje] = useState({})
    useEffect(() => {
        props.onPageLoad('Pregled izbornih grupa')
        props.onIzborneGrupeFetch()
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    useEffect(() => {
        setGrupePoGodinama(props.grupe_po_godinama)

    }, [props.grupe_po_godinama])

    const godine = []
    grupePoGodinama.forEach(el => {
        godine.push({godina: el.godina, active: false})
    })

    //metode za listenere za grupe i godine
    const onItemDeleteClicked = (grupa) => {
        setBrisanje(true)
        setGrupaZaBrisanje(grupa)
    }
    const onDeleteConfirmed = () => {
        modalClosed()
        axios.post('http://127.0.0.1:8000/studserviceapp-api/obrisi-grupu/', grupaZaBrisanje)
            .then(response => {
                const noveGrupe = trenutneGrupe.filter(grupa => {
                    return grupa !== grupaZaBrisanje
                })
                setTrenutneGrupe(noveGrupe)
            })
    }
    const godinaOnClick = (godina) => {
        switch (godina) {
            case 'I godina':
                setTrenutneGrupe(grupePoGodinama[0].grupe)
                break
            case 'II godina':
                setTrenutneGrupe(grupePoGodinama[1].grupe)
                break
            case 'III godina':
                setTrenutneGrupe(grupePoGodinama[2].grupe)
                break
            case 'IV godina':
                setTrenutneGrupe(grupePoGodinama[3].grupe)
                break
            default:
                break
        }
    }
    const grupaOnClick = (grupa) => {
        setGrupaZaIzmenu(grupa)
        setMenjanje(true)
    }

    const modalClosed = () => {
        setMenjanje(false)
        setBrisanje(false)
    }
    let body = (
        <div className={classes.IzmenaIzbornihGrupa}>
            <PregledGrupa godine={godine} grupe={trenutneGrupe} godinaOnClick={godinaOnClick}
                          grupaOnClick={grupaOnClick} onItemDeleteClicked={onItemDeleteClicked}/>
            <Modal show={menjanje} modalClosed={modalClosed}>
                {grupaZaIzmenu !== null ? <IzmenaGrupe grupa={grupaZaIzmenu}/> : null}
            </Modal>
            <Modal show={brisanje} modalClosed={modalClosed}>
                <div className={classes.DeleteWarning}>
                    <p className={classes.DeleteHeading}>Da li ste sigurni da zelite da obrisete grupu?</p>
                    <div className={classes.DeleteOptions}>
                        <Button buttonType='Danger' clicked={onDeleteConfirmed}>DA</Button>
                        <Button buttonType='Success' clicked={modalClosed}>NE</Button>
                    </div>
                </div>
            </Modal>
        </div>
    )
    if (props.loading) {
        body = (
            <div className={classes.IzmenaIzbornihGrupa}>
                <Spinner/>
            </div>
        )
    }
    return (
        body
    )
}

const mapStateToProps = state => {
    return {
        pageTitle: state.pageReducer.pageTitle,
        grupe_po_godinama: state.izborneGrupeReducer.grupe_po_godinama,
        loading: state.izborneGrupeReducer.loading
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onPageLoad: (title) => dispatch(actions.onPageLoad(title)),
        onIzborneGrupeFetch: () => dispatch(actions.fetchIzborneGrupe())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(IzmenaIzbornihGrupa)