import React, {useState} from "react";
import classes from './NavbarMenu.module.css'
import AccountItem from "./AccountItem/AccountItem";
import DrawerToggle from "../SideNav/DrawerToggle/DrawerToggle";
import SideNav from "../SideNav/SideNav";
import {connect} from 'react-redux'
const NavbarMenu = props => {
    const [showSideNav, setShowSideNav] = useState(false)
    const [showBackdrop, setShowBackdrop] = useState(false)
    const onClickSideDrawer = () => {
        setShowSideNav(true)
        setShowBackdrop(true)
    }
    const onBackdropClicked = () => {
        setShowBackdrop(false)
        setShowSideNav(false)
    }
    return (
        <div>
            <SideNav showSideNav={showSideNav} showBackdrop={showBackdrop} onBackdropClicked={onBackdropClicked}/>
            <nav className={classes.NavbarMenu}>
                <ul className={classes.NavbarList}>
                    <li>
                        <DrawerToggle toggleClicked={onClickSideDrawer}/>
                    </li>
                    <li>
                        {props.pageTitle}
                    </li>
                    <AccountItem/>
                </ul>
            </nav>
        </div>
    )
}
const mapStateToProps = state => {
    return {
        pageTitle: state.pageReducer.pageTitle
    }
}
export default connect(mapStateToProps)(NavbarMenu)