import React, {useEffect, useState} from 'react';
import {MDBDataTable} from 'mdbreact';
import classes from './Raspored.module.css'
import Spinner from "../UI/Spinner/Spinner";
import {connect} from 'react-redux'
import * as actions from '../../store/actions/index'

const Raspored = ({onInitRaspored, ...props}) => {
    const [data, setData] = useState({})
    useEffect(() => {
        onInitRaspored()
    }, [onInitRaspored])
    useEffect(() => {
        setData({
            columns: [
                {
                    label: 'Predmet',
                    field: 'naziv_predmeta',
                    sort: 'text',
                    width: 150
                },
                {
                    label: 'Predavac',
                    field: 'ime_prezime_nastavnika',
                    sort: 'asc',
                    width: 270
                },
                {
                    label: 'Grupe',
                    field: 'grupe',
                    sort: 'asc',
                    width: 200
                },
                {
                    label: 'Ucionica',
                    field: 'oznaka_ucionice',
                    sort: 'asc',
                    width: 100
                },
                {
                    label: 'Dan',
                    field: 'dan',
                    sort: 'asc',
                    width: 150
                },
                {
                    label: 'Pocetak',
                    field: 'pocetak',
                    sort: 'asc',
                    width: 100
                },
                {
                    label: 'Zavrsetak',
                    field: 'zavrsetak',
                    sort: 'asc',
                    width: 100
                },
                {
                    label: 'Tip nastave',
                    field: 'tip-nastave',
                    sort: 'asc',
                    width: 100
                }
            ],
            rows: props.termini
        })
    }, [props.termini])

    let raspored = (
        <div className={classes.Raspored}>
            <MDBDataTable
                striped
                bordered
                hover
                responsive
                data={data}
            />
        </div>
    )
    if (props.loading) {
        raspored = (
            <div className={classes.Raspored}>
                <Spinner/>
            </div>
        )
    }
    return (
        <React.Fragment>
            {raspored}
        </React.Fragment>
    )
}

const mapStateToProps = state => {
    return {
        termini: state.rasporedReducer.raspored,
        loading: state.rasporedReducer.loading
    }
}
const mapDispatchToProps = dispatch => {
    return {
        onInitRaspored: () => dispatch(actions.fetchRaspored())
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Raspored);